* {
  margin: 0;
  padding: 0;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: 16px;
}

.profile {
  margin-left: 15px;
  margin-top: 15px;
  margin-bottom: 20px;
  width: 250px;
  border: 1px solid royalblue;
  border-radius: 10px;
  text-align: center;
  background-color: cadetblue;
  overflow: hidden;
}
ul {
  list-style: none;
}
.stats {
  background-color: aquamarine;
  display: flex;
}
.description {
  padding-top: 10px;
  margin-bottom: 10px;
}
li {
  border: 1px solid royalblue;
}
span {
  display: inline-block;
}
