.friend_list {
  width: 300px;
  margin-bottom: 5px;
}
.item {
  display: flex;
  align-items: center;
}
.name {
  font-size: 24px;
  padding-left: 15px;
}
.status {
  display: block;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  margin-left: 10px;
  margin-right: 20px;
}
.isOnline {
  composes: status;
  background-color: green;
}
.notOnline {
  composes: status;
  background-color: red;
}
